@use "global" as *;

.wp-block-list {
    margin-top: rem(24);
    @include mq("md") {
        margin-top: rem(40);
    }
}

.wp-block-list li {
    padding-left: rem(21);
    position: relative;
    color: $blue;
}

.wp-block-list li::before {
    position: absolute;
    content: url(../images/common/hitode-blue.svg);
    top: rem(2);
    left: rem(-0.25);
}